export const cleanHostname = (dirtyHost = "") =>
    dirtyHost
        .toLowerCase()
        .replace(/[<>]/g, "") //get rid of brackets
        .replace(/http[s]?:\/\//g, "") //get rid of protocol
        .replace("www.", "") //get rid of redundant subdomain
        .replace(/\/$/, "")
        .trim();

export const uuidCreate = function() {
    // https://stackoverflow.com/questions/105034/create-guid-uuid-in-javascript
    return "xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx".replace(/[xy]/g, function(c) {
        let r = Math.random() * 16 | 0;
        let v = c == 'x' ? r : (r & 0x3 | 0x8);
        return v.toString(16);
    });
};

export const uuidSet = (uuid) => simpleStorageSet({uuid});

export const uuidGet = async function() {
    let uuid = await getUUID();
    if (!uuid) {
        uuid = utils.uuidCreate();
        await utils.uuidSet(uuid);
    }
    return uuid;
};

/**
 * Finds if there is a substring match in mismatched strings
 * @param {string} needle - String in which to check various substrings of the "haystack"
 * @param {string} haystack - String you want to search in
 * @param {number} minSubstringLength - defaults to 1
 * @return {boolean}
 * @example
 * // returns  true
 * hasSubstringMatch('dbfreteonline', 'dbfreteComp.exe', 5)
 *
 * @see https://stackoverflow.com/a/13007065/4875295
 */
export const hasSubstringMatch = (needle, haystack, minSubstringLength = 1) => {
    // Search possible substrings from largest to smallest:
    for (let i = needle.length; i >= minSubstringLength; i--) {
        for (let j = 0; j <= needle.length - i; j++) {
            const substring = needle.substr(j, i);
            if (haystack.includes(substring)) {
                return true;
            }
        }
    }
    return false;
};

/**
 * @param {string} url - A valid URL
 * @return {string} - The hostname of the URL
 */
export const urlHost = function(url) {
    if (url == null || url === "") {
        return "";
    }

    if (isBrowserPage(url)) {
        return "";
    }

    if (url.toLowerCase().startsWith("data:")) {
        return "";
    }

    if (url.toLowerCase().startsWith("blob:")) {
        url = url.substring(5);
    }

    try {
        if (
            !url.toLowerCase().startsWith('http://') &&
            !url.toLowerCase().startsWith('https://')
        ) {
            url = `http://${url}`;
        }
        return new URL(url).hostname;
    } catch (err) {
        console.debug('UH: invalid URL ', url, ' ', err);
        return url;
    }
};

export const urlTLD = function(url) {
    try {
        if (!url) {
            console.debug('UT: Invalid url passed: ', url);
            return '';
        }
        const tld = getPublicSuffix(url, {allowPrivateDomains: true});

        if (!tld) {
            console.debug("UT: TLD parsing error for ", url);
            return '';
        }

        return tld ? tld.toLowerCase() : '';
    } catch (err) {
        console.error("UT Error: ", err);
        return '';
    }
};

/**
 * Finds all domains in a host, excluding the TLD
 * @param {string} host - The URL's hostname
 * @return {Array<string>} An array of the all domains in a host lowercased, excluding the TLD
 * @example
 * // returns  ['instalki', 'download']
 * domainList('download.instalki.co.uk')
 */
export const domainList = (host) => {
    if (!host) {
        return [];
    }

    const sld = getDomainWithoutSuffix(host);
    const subdomain = getSubdomain(host);

    if (!sld && !subdomain) {
        console.debug("DL: Domain parsing error for: ", host);
        return [];
    }

    return [
        ...(sld ? [sld] : []),
        ...(subdomain ? subdomain.split(".") : []),
    ].map((domain) => domain.toLowerCase());
};

/**
 * Find a URL host's Subdomain
 * @param {string} host - The URL's hostname
 * @return {string} The host's subdomain lowercased | empty string if no subdomain
 * @example
 * // returns  'download'
 * subdomain('download.instalki.co.uk')
 * // returns  ''
 * subdomain('instalki.co.uk')
 * // returns  'whytho.cool'
 * subdomain('whytho.cool.instalki.co.uk')
 */
export const subdomain = (host) => {
    if (!host) {
        return '';
    }

    const sub = getSubdomain(host, {allowPrivateDomains: true});

    if (!sub) {
        console.debug("SD: Subdomain parsing error for: ", host);
        return '';
    }

    return sub.toLowerCase();
};

/**
 * Find a URL's domain
 * @param {string} url - The URL
 * @return {string} The host's domain lowercased | empty string if no domain could be gotten
 * @example
 * // returns  'instalki.co.uk'
 * domain('https://download.instalki.co.uk')
 * // returns  'instalki.co.uk'
 * domain('instalki.co.uk')
 * // returns  'instalki.co.uk'
 * domain('https://whytho.cool.instalki.co.uk')
 * // returns  'rfrs38m9.s3.us-east-2.amazonaws.com'
 * domain('https://rfrs38m9.s3.us-east-2.amazonaws.com/')
 */
export const domain = (url) => {
    if (!url) {
        return '';
    }

    if (isBrowserPage(url)) {
        return '';
    }

    // Perhaps we can deprecate private domains (e.g. s3.amazonaws.com) later, but I left it in for now since that's how the previous package worked
    const domain = getDomain(url, {allowPrivateDomains: true});

    if (!domain) {
        console.debug("D: Domain parsing error for: ", domain);
        return '';
    }

    return domain;
};

/**
 * Find a URL host's SLD (second level domain)
 * @param {string} host - The URL's hostname
 * @return {string} The host's SLD lowercased
 * @example
 * // returns  'instalki'
 * sld('download.instalki.co.uk')
 */
export const sld = (host) => {
    if (!host) {
        return '';
    }

    const sldd = getDomainWithoutSuffix(host, {allowPrivateDomains: true});

    if (!sldd) {
        console.debug("SLD: Domain parsing error for: ", host);
        return '';
    }

    return sldd.toLowerCase();
};

export const urlExt = (url) => {
    if (!url) {
        console.debug('UE: Params Error: No URL passed');
        return null;
    }

    // find and remove "//" from the beginning of the URL
    if (url.includes('//')) {
        url = url.substring(url.indexOf('//') + 2);
    }

    // Find the last slash index, ensuring it's not the first character
    const lastSlashIndex = url.lastIndexOf('/');

    // Check if there's a slash and dot separator within the valid filename area
    if (lastSlashIndex !== -1 && url.indexOf('.', lastSlashIndex + 1) !== -1) {
    // Split the string after the last slash and get the last part
        let location = lastSlashIndex + 1;
        let fileName = '';
        while (location < url.length && url[location] !== '?') {
            fileName += url[location];
            location++;
        }
        let parts = fileName.split('.');
        // remove empty strings (could happen if a url ends with a dot)
        parts = parts.filter((part) => part !== '');
        if (parts.length > 1) {
            return parts[parts.length - 1].toLowerCase();
        }
    }

    // No valid extension found
    return null;
};

let urlDomainsCache = {};

export const urlDomains = (url) => {
    let host = urlHost(url);
    if (!host) {
        return [];
    }
    if (urlDomainsCache[url]) {
        return urlDomainsCache[url];
    }

    let parts = host.split('.');
    let domains = [];
    let count = 0;
    do {
        domains.push(parts.slice(count).join("."));
        count++;
    } while (count < parts.length - 1);

    if (domains.length > 1) {
        let publicSuffix = getPublicSuffix(url, {allowPrivateDomains: true}) || '';
        // domain could be a local name such as http://wsb:8501
        // in this case, the publicSuffix is the same as the domain
        domains = domains.filter((domain) => domain !== publicSuffix);
    }
    urlDomainsCache[url] = domains;
    return domains;
};

