export const iconBadge = function(tabId, text, color) {
    chrome.tabs.get(tabId, (_tab) => {
        if (!chrome.runtime.lastError) {
            (chrome.action || chrome.browserAction).setBadgeText({tabId: tabId, text: text});
            (chrome.action || chrome.browserAction).setBadgeBackgroundColor({tabId: tabId, color: color});
        }
    });
};


/* Binary */

export const binaryToString = function(arrayBuffer) {
    let binaryArray;
    if (arrayBuffer.constructor.name == 'Uint8Array') {
        binaryArray = arrayBuffer;
    } else {
        binaryArray = new Uint8Array(arrayBuffer);
    }
    return binaryArray.reduce((str, byte) => str + String.fromCharCode(byte), '');
};

export const binaryFromString = function(str) {
    return Uint8Array.from(str, (char) => char.charCodeAt(0)).buffer;
};

//
// binary ArrayBuffer to/from base64-encoded ASCII string
//

export const binaryToBase64 = function(arrayBuffer) {
    return btoa(utils.binaryToString(arrayBuffer));
};

export const binaryFromBase64 = function(str) {
    return utils.binaryFromString(atob(str));
};

export const objToBinary = (obj) => {
    return binaryFromString(JSON.stringify(obj));
};

/**
* Check if the given bytes array is a Zstd file
* @param {Uint8Array} binaryArray - The array of bytes to check
* @return {boolean} - True if the array is a Zstd file, false otherwise
*/
export const isZstd = (binaryArray) => {
    if (binaryArray.length < 4) {
        return false;
    }
    //Find the Magic Number 0xFD2FB528 (little-endian)
    //https://datatracker.ietf.org/doc/html/rfc8878#section-3.1.1
    return binaryArray[0] == 40 && binaryArray[1] == 181 &&
        binaryArray[2] == 47 && binaryArray[3] == 253;
};

export let browserMem = {};
Object.defineProperty(browserMem, 'browser', {writable: true});

/**
 * Get the user's browser name
 * @return {string} "Firefox", "Opera", "Edge", "Chrome", "Safari" or "Browser"
 */
export const browserName = function() {
    if (browserMem.browser) {
        return browserMem.browser;
    }
    if (navigator.userAgent.includes("Firefox/")) {
        return (browserMem.browser = "Firefox");
    }
    if (
        navigator.userAgent.includes("Opera/") ||
        navigator.userAgent.includes("OPR/")
    ) {
        return (browserMem.browser = "Opera");
    }
    if (
        navigator.userAgent.includes("Edge/") ||
        navigator.userAgent.includes("Edg/")
    ) {
        return (browserMem.browser = "Edge");
    }
    if (navigator.userAgent.includes("Chrome/")) {
        return (browserMem.browser = "Chrome");
    }

    if (navigator.userAgent.includes("Safari/")) {
        return (browserMem.browser = "Safari");
    }

    return "Browser";
};

export let osMem = {};
Object.defineProperty(osMem, 'os', {writable: true});

export const osName = () => {
    return new Promise((res) => {
        osMem.os ? res(osMem.os) : chrome.runtime.getPlatformInfo(({os}) => {
            osMem.os = os;
            res(osMem.os);
        });
    });
};

/* Redirect */

export const createBlockUrl = function(
    optionalReferrer,
    url,
    type,
    subtype,
    tabId,
    optionalFilename,
    optionalPrevUrl,
    rule
) {
    return (
        chrome.runtime.getURL(`app/eventpages/${isMV3() ? 'block-mv3.html' : 'block.html'}`) +
        "?" +
        redirectParameters({
            referrer: optionalReferrer,
            url,
            host: urlHost(url),
            type,
            subtype,
            tabId,
            filename: optionalFilename,
            prevUrl: optionalPrevUrl || null,
            rule
        })
    );
};

/**
 * Converts an object of parameters into a URL-encoded string.
 * @param {Object} parameters - The parameters to be converted.
 * @returns {string} - The URL-encoded string.
 */
export const redirectParameters = function(parameters) {
    let searchParams = new URLSearchParams;
    for (let key in parameters) {
        if (Object.hasOwn(parameters, key)) {
            searchParams.set(key, parameters[key]);
        }
    }
    return searchParams.toString();
};

/* Fetch */

export const fetchText = async function(url, options = {}) {
    let response = await fetch(url, utils.fetchOptions(options));
    let contents = await response.text();
    return contents;
};

export const fetchJSON = async function(url, options = {}) {
    let response = await fetch(url, utils.fetchOptions(options));
    let contents = await response.json();
    return contents;
};

export const fetchBinary = async function(url, options = {}) {
    let response = await fetch(url, utils.fetchOptions(options));
    return await response.arrayBuffer();
};

export const fetchOptions = function(options) {
    options.method = options.method || "GET";
    options.headers = options.headers || {};
    options.headers["pragma"] = "no-cache";
    options.headers["cache-control"] = "no-cache, no-store";

    return options;
};

export const injectJavascript = function(scriptContents) {
    let script = document.createElement("script");
    script.textContent = scriptContents;
    let parent = (document.head || document.documentElement);
    parent.insertBefore(script, parent.firstChild);
    script.remove();
};

export const meas = (thisStep, prevStep) => {
    performance.mark(thisStep);
    if (prevStep) {
        performance.measure(
            thisStep + '_time',
            prevStep,
            thisStep
        );
    }
};

// Test cases https://regexr.com/5n86j
export const isValidIP = (ip = '') => isIp.test(ip);

// Test if the given string is a valid domain name or an IP address
export function isValidUrl(domain) {
    try {
        if (isValidIP(domain)) {
            return true;
        }
        const removeRedundant = domain.replace(/^(https?:\/\/)?(www\.)?/i, '');
        let str = cleanHostname(removeRedundant);
        if (!str.startsWith('http') && (!str.includes('.') || str.endsWith('.'))) {
            return false;
        }
        // A domain name consists of minimum two and maximum 63 characters.
        if (str.length < 3 || str.length > 253) {
            return false;
        }
        let url = new URL(str.startsWith('http') ? str : `http://${str}`);
        return !!url;
    } catch (error) {
        return false;
    }
}

