const AD_TELEM_DATE = 'adTelemDate';
export const hasOneDayPassed = () => {
    return new Promise((resolve) => {
        const date = new Date().toLocaleDateString();

        simpleStorageGet(AD_TELEM_DATE).then(lastSent => {
            if (lastSent === date) {
                resolve(false);
            } else {
                simpleStorageSet({[AD_TELEM_DATE]: date}).then(() => resolve(true));
            }
        });
    });
};


