/**
 * @param {string} fileName - js file to be injected into page
 * @param {object} searchParams - object to be passed to the injected script as query parameters
 * @note Remember to add your script to web_accessible_resources in the manifest
 */
export const injectJsFile = (fileName, searchParams) => {
    let parent = document.head || document.documentElement;
    let script = document.createElement("script");
    if (searchParams) {
        script.src = chrome.runtime.getURL(`${fileName}?`) + new URLSearchParams(searchParams);
    } else {
        script.src = chrome.runtime.getURL(fileName);
    }

    script.onload = function() {
        this.remove();
    };
    parent.appendChild(script);
};

export const downloadLogsToFile = (logDataStr) => {
    const logData = logDataStr;
    const url = URL.createObjectURL(new Blob([logData], {type: 'text/plain'}));
    let dlDownloadName;
    const time = new Date();
    dlDownloadName = `BG-Logs_v${chrome.runtime.getManifest().version}_` +
        `${time.toISOString().slice(0, 10)}_${time.getHours()}${time.getMinutes()}${time.getSeconds()}.txt`;

    let options = {url, filename: dlDownloadName, conflictAction: 'uniquify'};
    // now add 'saveAs: false' if the browser is firefox due to an ff bug
    if (TARGET_BROWSER === 'Firefox') {
        options.saveAs = false;
    }

    chrome.downloads.download(options).then(() => {
        console.log('Successfully Downloaded logs');
    }).catch((err) => {
        console.error('Error downloading logs', err);
    });
    // use chrome.downloads.onChange event to remove the URL once the download is complete
    chrome.downloads.onChanged.addListener((download) => {
        if (download.state && download.state.current === 'complete') {
            URL.revokeObjectURL(url);
        }
    });
};

/**
 * Checks if a given URL belongs to an internal browser page.
 * @param {string} url - The URL to check.
 * @returns {boolean} - Returns true if the URL belongs to an internal browser page, otherwise returns false.
 */
export const isBrowserPage = (url) => {
    if (!url || url === '') {
        return true;
    }
    return url.startsWith('chrome-extension://') ||
    url.startsWith('moz-extension://') ||
    url.startsWith('edge-extension://') ||
    url.startsWith('edge://') ||
    url.startsWith('file://') ||
    url.startsWith('about:') ||
    url.startsWith('chrome-devtools://') ||
    url.startsWith('devtools://') ||
    url.startsWith('chrome://');
}

/**
 * Checks if the given URL belongs to a private network.
 *
 * @param {string} url - The URL to check.
 * @returns {boolean} Returns true if the URL belongs to a private network, false otherwise.
 */
export const isPrivateNetwork = (url) => {
    if (!url || url === '') {
        return true;
    }

    if (isBrowserPage(url)) {
        return true;
    }

    const urlObj = new URL(url);
    return urlObj.hostname === 'localhost' ||
        urlObj.hostname.startsWith('127.') ||
        urlObj.hostname.startsWith('10.') ||
        urlObj.hostname.startsWith('192.168.') ||
        urlObj.hostname.startsWith('172.');
};

export const getRandomNumberInRange = (min, max)=>{
    const randomDecimal = Math.random();
    const randomNumber = min + randomDecimal * (max - min);
    return randomNumber;
};

export const fullJitterBackoffLogic = (attempt, cap, baseTime) =>{
    let delayTime = getRandomNumberInRange(0, Math.min(cap, baseTime * (2 ** attempt)));
    delayTime = Math.floor(delayTime);
    return delayTime;
};

export function parseParams(searchParams) {
    const handler = {
        get(target, prop) {
            if (prop.startsWith('$')) {
                return target[prop];
            }
            return target[`$${prop}`];
        }
    };

    let params = {};
    let pairs = new URLSearchParams( searchParams ).entries();

    for (let pair of pairs) {
        const name = '$' + pair[0];
        if (pair[1] === 'null') {
            params[name] = null;
        } else if (pair[1] === 'undefined') {
            params[name] = undefined;
        } else {
            params[name] = pair[1];
        }
    }
    const proxy = new Proxy(params, handler);
    return proxy;
}

/**
 * Calculates a fast hash value for the given string.
 * @param {string} str - The input string to calculate the hash for.
 * @returns {number} The calculated hash value as a 32-bit integer.
 */
export function fastHash(str) {
    let hash1 = 5381;
    let hash2 = 0;
    if (str.length === 0) {
        return hash1;
    }
    for ( let c of str ) {
        hash1 = ( 33 * hash1 + c.charCodeAt(0) ) >>> 0;
        hash2 = ( 65599 * hash2 + c.charCodeAt(0) ) >>> 0;
    }

    let hash = (hash1 ^ hash2) >>> 0;
    return hash;
}

export function getIncrementalPathSegments(url) {
    if (!url) {
        return [];
    }
    let urlObj;
    try {
        urlObj = new URL(url);
    } catch (error) {
        return [];
    }

    let parts = urlObj.pathname.split('/');
    let paths = [];
    let count = 0;
    do {
        paths.push(parts.slice(count).join("/"));
        count++;
    } while (count < parts.length);
    return paths;
}


export const injectCSS = (cssFileName, cssContents) => {
    let parent = document.head || document.documentElement;
    let styleElement = document.createElement("style");

    if (cssFileName) {
        // Link to external CSS file
        let linkElement = document.createElement("link");
        linkElement.rel = "stylesheet";
        linkElement.href = chrome.runtime.getURL(cssFileName);
        parent.appendChild(linkElement);
    } else if (cssContents) {
        // Inject inline CSS
        styleElement.textContent = cssContents;
        parent.appendChild(styleElement);
    }
};


/**
 * Clean the object properties by a template object
 * @param {object} ObjToClean - The object to clean
 * @param {object} Template - The template object
 * @param {boolean} setDefault - If true, set the default value from the template if the value is falsy
 * @returns {object} - The cleaned object
 */
export const cleanObjectPropsByTemplate = (ObjToClean, Template, setDefault = false) => {
    // Get the allowed properties from the template
    const allowedProps = Object.keys(Template);

    // Create a new object with only the allowed properties
    const cleanedObject = {};

    // Iterate over the allowed properties and add them to the cleaned object
    allowedProps.forEach((key) => {
        if (Object.hasOwn(ObjToClean, key)) {
            cleanedObject[key] = ObjToClean[key];
        } else if (setDefault) {
            cleanedObject[key] = Template[key];
        }
    });

    return cleanedObject;
};

/**
 * Get absolute xPath position from dom element
 * xPath position will does not contain any id, class or attribute, etc selector
 * Because, Some page use random id and class. This function should ignore that kind problem, so we're not using any selector
 *
 * @param {Element} element element to get position
 * @returns {String} xPath string
 */
export function getXPath(element) {
    // Selector
    let selector = '';
    // Loop handler
    let foundRoot;
    // Element handler
    let currentElement = element;

    // Do action until we reach html element
    do {
        // Get element tag name
        const tagName = currentElement.tagName.toLowerCase();
        // Get parent element
        const parentElement = currentElement.parentElement;

        // Count children
        if (parentElement && parentElement.childElementCount > 1) {
            // Get children of parent element
            const parentsChildren = [...parentElement.children];
            // Count current tag
            let tag = [];
            parentsChildren.forEach(child => {
                if (child.tagName.toLowerCase() === tagName) {
                    tag.push(child); // Append to tag
                }
            })

            // Is only of type
            if (tag.length === 1) {
                // Append tag to selector
                selector = `/${tagName}${selector}`;
            } else {
                // Get position of current element in tag
                const position = tag.indexOf(currentElement) + 1;
                // Append tag to selector
                selector = `/${tagName}[${position}]${selector}`;
            }

        } else {
            //* Current element has no siblings
            // Append tag to selector
            selector = `/${tagName}${selector}`;
        }

        // Set parent element to current element
        currentElement = parentElement;
        if (currentElement == null)
        {
            break;
        }
        // Is root
        foundRoot = parentElement.tagName && parentElement.tagName.toLowerCase() === 'html';
        // Finish selector if found root element
        if(foundRoot) {
            selector = `/html${selector}`;
        }
    }
    while (foundRoot === false);

    // Return selector
    return selector;
}

