export const getBrowserInformation = async () => {

    if (navigator.userAgent) {
        return parseUserAgent(navigator.userAgent);
    }

    if (navigator.userAgentData && navigator.userAgentData.getHighEntropyValues) {
        try {
            const uaData = await navigator.userAgentData.getHighEntropyValues(
                [
                    "architecture",
                    "model",
                    "platformVersion",
                    "fullVersionList"
                ]
            );
            if (uaData.fullVersionList && uaData.fullVersionList.length > 0) {
            // Find the browser with the highest priority (usually the first one)
                const browser = uaData.fullVersionList[0];
                return {
                    name: browser.brand,
                    version: browser.version,
                    os: {name: uaData.platform, version: uaData.platformVersion},
                    cpu: {architecture: uaData.architecture, model: uaData.model},
                    fullInfo: uaData
                };
            }
        } catch (error) {
            console.error("Error getting high entropy values:", error);
            // Fallback to userAgent
        }
    }

    if (browser != null && browser.runtime && browser.runtime.getBrowserInfo) {
        const browserInfo = await browser.runtime.getBrowserInfo();
        const osInfo = await browser.runtime.getPlatformInfo();
        return {
            name: "Firefox", //Or other browser name.
            version: browserInfo.version,
            os: {name: osInfo.os || "Unknown", version: osInfo.os_version || "Unknown"},
            cpu: {architecture: osInfo.arch || 'Unknown'},
            device: {type: osInfo.vendor || 'Unknown'}
        };
    }

    return null; // Unable to determine browser version
}

export function parseUserAgent(userAgent) {
    // use the useragent parser library
    const parsed = UAParser(userAgent)

    let version = parsed.browser.version || '0.0.0';
    let name = parsed.browser.name || 'Unknown';
    let cpu = {architecture: parsed.cpu.architecture || 'Unknown'};
    let device = {model: parsed.device.model || 'Unknown'};
    let os = { name: parsed.os.name || 'Unknown', version: parsed.os.version || 'Unknown'};

    while (version.split('.').length < 3) {
        version += '.0';
    }

    return {
        name,
        version,
        cpu,
        device,
        os,   
    };
}

export const throttleFunc = (func, limit) => {
    let inThrottle;
    return function() {
        const args = arguments;
        const context = this;
        if (!inThrottle) {
            func.apply(context, args);
            inThrottle = true;
            setTimeout(() => inThrottle = false, limit);
        }
    }
};

/**
 * Check if the given URL is a block page.
 * @param {string} UrlString - The URL to check
 * @returns {boolean} - Returns true if the URL is a block page, otherwise false.
 */ 
export function isBlockPage(UrlString) {
    if (!UrlString) {
        return false;
    }
    const url = new URL(UrlString);
    return url.pathname.endsWith('block-mv3.html');
}

/**
 * Get the blocked host URL from the given URL string.
 * @param {string} UrlString - The URL string to extract the blocked host from
 * @returns {string|null} - Returns the blocked host URL if found, otherwise null.
 */
export function getBlockedHostURL(UrlString) {
    if (!UrlString) {
        return null;
    }
    const url = new URL(UrlString);
    return url.searchParams.get('host');
}


/**
 * Get the tab by its ID.
 * @param {number} tabId - The ID of the tab to retrieve
 * @returns {Promise<chrome.tabs.Tab|null>} - Returns a promise that resolves to the tab object if found, otherwise null.
 * @throws {Error} - Throws an error if the tab cannot be retrieved.
*/
export async function getTabById(tabId) {
    if (tabId === -1 || !tabId) {
        return null;
    }

    return new Promise((resolve, reject) => {
        chrome.tabs.get(tabId, (tab) => {
            if (chrome.runtime.lastError) {
                reject(chrome.runtime.lastError);
            } else {
                resolve(tab);
            }
        });
    });
}

/**
 * Get the currently active tab in the current window.
 * @returns {Promise<chrome.tabs.Tab|null>} - A promise that resolves to the current tab or null if not found.
 */
export async function getCurrentTab() {
    let queryOptions = { active: true, currentWindow: true };
    let [tab] = await chrome.tabs.query(queryOptions);
    return tab;
}
