import {MSG_DOWNLOAD_LOGS} from './app-consts';
import {chrome} from '../../utils/polyfill.ts';
import {translateText} from '../../utils/locales.ts';
import {downloadLogsToFile} from '../../utils/utils';
window.addEventListener('DOMContentLoaded', ()=>{
    translateText([
        {id: 'download-status', msg: 'downloadingStatus'},
    ]);
    document.getElementById("progress-percentage").textContent = '65%';
    document.querySelector('.debug-page-header').classList.remove('hidden');
});
window.addEventListener("load", () => {
    const downloadImage = document.querySelector(".download-debug-logs-content .img-container");
    const downloadStatusTxt = document.getElementById("download-status");
    const downloadProgressBar = document.querySelector(".download-debug-logs-content .progress-bar-container");
    const downloadProgressPercentage = document.getElementById("progress-percentage");
    const downloadErrorFooter = document.querySelector(".download-debug-logs-content .debug-error-footer");

    chrome.runtime.sendMessage(
        {type: MSG_DOWNLOAD_LOGS, fullLog: true},
        (resp) => {
            if (!resp || resp.error) {
                translateText([
                    {id: 'debug-error-message', msg: 'debugErrorMessage'},
                    {id: 'support-txt', msg: 'supportTxt'}
                ]);
                downloadStatusTxt.textContent = chrome.i18n.getMessage("downloadErrorStatus");
                downloadImage.classList.remove('downloading');
                downloadImage.classList.add('error');
                downloadProgressBar.classList.remove('downloading');
                downloadProgressBar.classList.remove('pulse-animation');
                downloadProgressBar.classList.add('error');
                downloadProgressPercentage.textContent = chrome.i18n.getMessage("downloadError");
                downloadErrorFooter.classList.remove('hidden');
            } else {
                downloadStatusTxt.textContent = chrome.i18n.getMessage("downloadCompleteStatus");
                downloadImage.classList.remove('downloading');
                downloadImage.classList.add('complete');
                downloadProgressBar.classList.remove('downloading');
                downloadProgressBar.classList.remove('pulse-animation');
                downloadProgressBar.classList.add('complete');
                downloadProgressPercentage.textContent = '100%';
                downloadLogsToFile(resp.data);
            }
        }
    );
});
